@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#map .list-box {
  display: flex;
  justify-content: space-between;
  column-gap: 16px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 992px) {
  #map .list-box {
    flex-direction: column;
    row-gap: 20px;
  }
}
#map .list-box__list {
  display: flex;
  flex-direction: column;
  row-gap: 9px;
  width: 100%;
}
@media (max-width: 992px) {
  #map .list-box__list {
    row-gap: 20px;
  }
}
#map .list-box__list li {
  position: relative;
  padding-left: 30px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  color: #2b2b2b;
}
@media (max-width: 768px) {
  #map .list-box__list li {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
  }
}
#map .list-box__list li:before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-100%);
  width: 15px;
  height: 11px;
  content: url(data:image/svg+xml;base64,);
}
#map .content-container_map {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0;
  margin-top: 77px;
  height: 745px;
  max-height: 66vh;
  overflow: hidden;
  margin-top: 50px;
}
#map .content-container_map .map-item {
  height: 745px;
  max-height: 67vh;
}
#map .content-container_map .ymaps-2-1-79-ground-pane {
  filter: grayscale(100%);
}
#map .page-loader-wrap {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2b2b2b;
  transform: scale(1.5);
  opacity: 0.8;
  z-index: 10000;
}